//custom for social sites
.fab {
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    background-image: url("socialmedia.png");
    background-size: 1350%; //number of images horizontally * 100
    display: block;
}

.facebook {
    background-position: 3.2% 3.8%;
}
.twitter {
    background-position: 13.6% 4%;
}
.linkedin {
    background-position: 65.5% 3.8%;
}
.github {
    background-position: 23.90% 56.5%;
}
.about-section {
    padding: 80px 0 0;
    min-height: 100vh;
}
.about-section .about-img {
    flex: 0 0 40%;
    max-width: 40%;
    padding: 0 15px;
}
.about-section .about-img .img-box {
    padding: 15px;
    border-radius: 5px;
}
.about-section .about-img .img-box img {
    width: 100%;
    border: 10px solid transparent;
    border-radius: 5px;
}
.about-section .about-img .social-links {
    text-align: center;
}
.about-section .about-info {
    flex: 0 0 60%;
    max-width: 60%;
    padding: 0 15px;
}
.about-section .about-info p {
    font-size: 16px;
    line-height: 26px;
    margin-bottom: 15px;
    color: var(--text-black-600); 
}
.about-section .about-info span {
    font-weight: 600;
}
.about-section .about-info .btn-1 {
    margin: 30px 20px 0;
}
.social-links a {
    width: 30px;
    height: 30px;
    display: inline-block;
    border-radius: 50%;
    overflow: hidden;
    margin: 0px 8px 0px -2px;
}
.social-outer-shadow {
    box-shadow: var(--outer-shadow);
}
a.social-hover-in-shadow {
    position: relative;
    z-index: 1;
}
a.social-hover-in-shadow:hover {
    box-shadow: var(--outer-shadow-0);
}
.about-section .about-tabs {
    padding: 60px 0 0 0;
    flex: 0 0 100%;
    max-width: 100%;
    text-align: center;
}
.about-section .skills-tab {
    padding: 60px 15px 50px;
    flex: 0 0 100%;
    max-width: 100%;
}
.about-section .about-tabs .tab-item {
    display: inline-block;
    margin: 0 5px 10px;
    cursor: pointer;
    padding: 5px 10px;
    border-radius: 30px;
    transition: all 0.3s ease;
    font-size: 16px;
    color: var(--text-black-600);
    font-weight: 500;
    text-transform: capitalize;
}
.about-section .about-tabs .tab-item:hover:not(.active) {
    opacity: 0.6;
}
.about-section .about-tabs .tab-item.active {
    color: var(--skin-color);    
}
.about-section .tab-content {
    flex: 0 0 100%;
    max-width: 100%;
    display: none;
}
.about-section .tab-content.active {
    display: block;
    animation: fadeInTop 0.5s ease;
}
.about-section .skills {
    padding: 0 0 40px;
}
.about-section .skills .skill-item {
     flex: 0 0 50%;
     max-width: 50%;
     padding: 0 15px;
     margin-bottom: 40px;
}
.about-section .skills .skill-item p {
    font-size: 16px;
    color: var(--text-black-600);
    text-transform: capitalize;
    margin: 0 0 10px;
}
.about-section .skills .skill-item .progress {
    height: 20px;
    border-radius: 10px;   
    position: relative;
}
.about-section .skills .skill-item .progress-bar {
    background-color: var(--text-black-600);
    height: 6px;
    left: 7px;
    top: 7px;
    position: absolute;
    border-radius: 10px;
}
.about-section .skills .skill-item .progress-bar span {
    position: absolute;
    right: 0;
    top: 0;
    font-size: 16px;
    font-weight: 500;
    color: var(--text-black-600);
    margin-top: -40px;
}
.about-section .experience {
    padding-bottom: 80px;
}
.about-section .timeline {
    flex: 0 0 100%;
    max-width: 100%;
    position: relative;
}
.about-section .timeline:before {
    content: '';
    width: 1px;
    height: 100%;
    left: 50%;
    top: 0;
    background-color: var(--bg-black-100);
    position: absolute;
}
.about-section .timeline .timeline-item {
    flex: 0 0 100%;
    max-width: 100%;
    padding: 0 15px;
}
.about-section .timeline .timeline-item:nth-child(odd) {
    padding-right:  calc(50% + 50px);
    text-align: right;
}
.about-section .timeline .timeline-item:nth-child(even) {
    padding-left:  calc(50% + 50px);
}
.about-section .timeline .timeline-item-inner {
    padding: 30px;
    border-radius: 5px;
    position: relative;
}
.about-section .timeline .timeline-item-inner:before {
    content: '';
    position: absolute;
    width: 30px;
    height: 1px;
    background-color: var(--bg-black-100);
    top: 37px;
    z-index: -1;
}
.fas {
    background-image: url("commonsprite.png");
    background-size: 1300%; //number of images horizontally * 100
    display: block;
}
.briefcase {
    background-position: 41.6% 100%;
}
.certificate {
    background-position: 33.25% 100%;
}
.about-section .timeline .timeline-item-inner:before {
    content: '';
    position: absolute;
    width: 30px;
    height: 1px;
    background-color: red;
    top:37px
}
.about-section .timeline .timeline-item:nth-child(odd) .timeline-item-inner:before  {
    right: -30px;
}
.about-section .timeline .timeline-item:nth-child(even) .timeline-item-inner:before  {
    left: -30px;
}
.about-section .timeline .timeline-item-inner .icon {
    height: 40px;
    width: 40px;
    background-color: var(--bg-black-100);
    text-align: center;
    border: 1px solid var(--bg-black-100);
    line-height: 40px;
    border-radius: 50%;
    font-size: 16px;
    background-color: var(--skin-color);
    position: absolute;
    top: 18px;
}
.about-section .timeline .timeline-item:nth-child(odd) i {
    right: -70px;
}
.about-section .timeline .timeline-item:nth-child(even) i {
    left: -70px;
}
.about-section .timeline .timeline-item-inner span {
    font-weight: 500;
    color: var(--skin-color);
    display: block;
    margin: 0 0 10px;
    text-transform: capitalize;
}
.about-section .timeline .timeline-item-inner h3 {
    font-size: 20px;
    color: var(--text-black-700);
    font-weight: 600;
    margin: 0 0 5px;
    text-transform: capitalize;
}
.about-section .timeline .timeline-item-inner h4 {
    font-size: 16px;
    font-style: italic;
    color: var(--text-black-600);
    margin: 0;
}
.about-section .timeline .timeline-item-inner p {
    font-size: 16px;
    color: var(--text-black-600);
    line-height: 26px;
    margin: 15px 0 0;
}
@media(max-width: 767px) {
    .about-section .skills .skill-item,
    .about-section .about-info,
    .about-section .about-img {
        flex: 0 0 100%;
        max-width: 100%;
    }
    .about-section .about-img {
        padding-bottom: 30px;
    }
    .about-section .timeline .timeline-item:nth-child(odd) {
        padding-right: 15px;
        padding-left: 75px;
        text-align: left;
    }
    .about-section .timeline .timeline-item:nth-child(even) {
        padding-left: 75px;
    }
    .about-section .timeline::before {
        left: 35px;
    }
    .about-section .timeline .timeline-item:nth-child(odd) .icon {
        right: auto;
        left: -60px;
    }
    .about-section .timeline .timeline-item:nth-child(even) .icon {
        left: -60px;
    }
    .about-section .timeline .timeline-item {
        margin-bottom: 40px;
    }
    .about-section .timeline .timeline-item:last-child {
        margin-bottom: 0;
    }
    .about-section .timeline .timeline-item {
        margin-bottom: 40px;
    }
    .about-section .timeline .timeline-item:nth-child(odd) .timeline-item-inner::before {
        left: -30px;
        right: auto;
    }
}
